      render(<BirthdayPianoSurprise />);
      expect(screen.getByText('Special Surprise')).toBeInTheDocument();
      expect(screen.getByText(/This special birthday surprise is locked until September 23rd!/i)).toBeInTheDocument();
      expect(screen.getByText(/until unlock/i)).toBeInTheDocument();
